import album001 from './album001.js'
import album002 from './album002.js'
import onePiece from "./onePiece.js";
import dragonBall from "./dragonBall.js";

// 每个相册全部信息导出
export const photoList = [dragonBall, onePiece, album001, album002]

// 相册数据，每个相册中第一个元素信息
export const albumList = function () {
  const albumList = []
  photoList.forEach(item => {
    const obj = {
      ...item[0],
      num: item.length
    }
    albumList.push(obj)
  })
  return albumList
}()

// 每个相册全部信息导出
export const allPhotoList = function () {
  const allPhotoList = []
  photoList.forEach(item => {
    allPhotoList.push(...item)
  })
  return allPhotoList
}()

