<template>
  <div class="my-album-page">
    <div class="my-album-box">
      <!-- 头部布局 -->
      <div class="my-album-title">
        <p
          class="title-nav-album"
          :class="{ active: showListType === 'album' }"
          @click="switchNav('album')"
        >
          相册
        </p>
        <p
          class="title-nav-photo"
          :class="{ active: showListType === 'photo' }"
          @click="switchNav('photo')"
        >
          {{ albumName }}图片
        </p>
      </div>
      <!-- 内容布局 -->
      <div class="my-album-content-layout">
        <div class="my-album-content">
          <div class="album-list">
            <div
              v-for="(item, index) in showList"
              :key="index"
              class="album-list-item"
              @click="lookBigImg(index)"
            >
              <!-- 图片盒子 -->
              <div class="album-list-item-img">
                <img :src="item.photoImg" alt="" />
              </div>
              <!-- 信息描述 -->
              <div class="album-list-item-info" v-if="showListType === 'album'">
                <div class="item-left">
                  <p class="item-name">{{ item.albumName }}</p>
                  <p class="item-date">{{ item.photoDate }}</p>
                </div>

                <p class="item-num">{{ item.num }}</p>
              </div>
            </div>
          </div>
          <!-- <div class="photo-list"></div> -->
        </div>
      </div>
      <!-- 大图展示 -->
      <div class="big-img-zzc" v-show="bigImgUrl">
        <p class="num-show">{{ bigImgIndex + 1 }}/{{ showList.length }}</p>
        <!-- 关闭按键 -->
        <p class="off-img-btn iconfont icon-31guanbi" @click="offImg"></p>
        <!-- 其他功能盒子 -->
        <div class="tool-box">
          <!-- 下载 -->
          <p
            title="下载"
            class="download-btn iconfont icon-xiazai"
            @click="downloadImg"
          ></p>
          <!-- 放大 -->
          <p
            title="放大"
            class="enlarge-btn iconfont icon-fangda1"
            @click="enlargeFn(true)"
          ></p>
          <!-- 缩小 -->
          <p
            title="缩小"
            class="narrow-btn iconfont icon-suoxiao1"
            @click="enlargeFn(false)"
          ></p>
          <!-- 旋转 -->
          <p
            title="旋转"
            class="rotate-btn iconfont icon-zuoxuanzhuan"
            @click="rotateFn"
          ></p>
          <!-- 信息-->
          <p
            title="图片信息"
            class="xinxi-btn iconfont icon-xinxi1"
            @click="rotateFn"
          ></p>
          <!-- 分享-->
          <p
            title="分享链接"
            class="share-btn iconfont icon-gongxiang"
            @click="rotateFn"
          ></p>
        </div>
        <!-- 大图预览图 -->
        <img :src="bigImgUrl" alt="" class="big-preview" />
        <!-- 左右切换按键 -->
        <p
          class="switch-btn iconfont icon-qiehuanqizuo"
          @click="switchFn('prev')"
        ></p>

        <p
          class="switch-btn iconfont icon-qiehuanqiyou"
          @click="switchFn('next')"
        ></p>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { albumList, allPhotoList, photoList } from './myAlbum';
export default {
  name: '',
  setup() {
    const data = reactive({
      rotate: 0, //旋转角度
      zoom: 90, //缩放大小
      albumName: '全部', //照片文案
      showList: albumList, //当前显示的列表，可能是相册或者照片
      showListType: 'album', //当前类型，相册或照片
      bigImgUrl: '', //照片地址
      bigImgIndex: 0, //照片下标
      albumList,
      allPhotoList
    });
    onBeforeMount(() => {});
    onMounted(() => {
      // waterfallLayout();   js实现瀑布布局 效果不好
    });
    // const waterfallLayout = () => {
    //   // 获取整个容器
    //   const albumList = document.getElementsByClassName('album-list')[0];
    //   // 获取整个容器宽度
    //   const listW = albumList.offsetWidth;
    //   // 获取全部图片容器
    //   const imgEles = albumList.children;
    //   // 获取图片容器宽度
    //   const imgElesW = imgEles[0].offsetWidth;

    //   const rowNum = Math.floor(listW / imgElesW);
    //   let arrHeight = [];
    //   for (let i = 0; i < imgEles.length; i++) {
    //     if (i < rowNum) {
    //       imgEles[i].style.position = 'relative';
    //       //第一行
    //       arrHeight.push(imgEles[i].clientHeight);
    //     } else {
    //       const obj = {
    //         minH: arrHeight[0],
    //         minI: 0
    //       };
    //       for (let j = 0; j < arrHeight.length; j++) {
    //         if (arrHeight[j] < obj.minH) {
    //           obj.minH = arrHeight[j];
    //           obj.minI = j;
    //         }
    //       }
    //       imgEles[i].style.position = 'absolute';
    //       imgEles[i].style.left = imgEles[obj.minI].offsetLeft + 'px';
    //       imgEles[i].style.top = obj.minH + 20 + 'px';
    //       arrHeight[obj.minI] = arrHeight[obj.minI] + imgEles[i].clientHeight;
    //     }
    //   }
    // };
    // 导航切换
    const switchNav = (type) => {
      data.showListType = type;
      if (type === 'album') {
        data.showList = albumList;
        // 切换照片文案
        data.albumName = '全部';
      } else {
        data.showList = allPhotoList;
      }
    };
    // 查看大图预览
    const lookBigImg = (index) => {
      // 大图展示
      if (data.showListType === 'photo') {
        data.bigImgIndex = index; //保存当前大图index
        data.bigImgUrl = data.showList[index].photoImg;
        data.zoom = 90;
        handleeElargeFn();
        data.rotate = 0;
        handleeRotateFn();
      } else {
        data.showListType = 'photo';
        // 切换照片文案
        data.albumName = photoList[index][0].albumName;
        // 进入相册
        data.showList = photoList[index];
        console.log(data.showList);
      }
    };
    // 关闭大图预览
    const offImg = () => {
      data.bigImgUrl = '';
    };
    // 点击下载跳转至百度网盘地址
    const downloadImg = () => {
      window.open(data.showList[0].download);
    };
    // 放大预览
    const enlargeFn = (bool) => {
      if (bool) {
        // 放大限制
        if (data.zoom > 200) return;
        data.zoom += 10;
      } else {
        // 缩小限制
        if (data.zoom < 20) return;
        data.zoom -= 10;
      }

      handleeElargeFn();
    };
    // 处理放大缩小
    const handleeElargeFn = () => {
      const img = document.getElementsByClassName('big-preview')[0];
      img.style.width = `${data.zoom}%`;
      img.style.height = `${data.zoom}%`;
    };
    // 旋转
    const rotateFn = () => {
      data.rotate -= 90;
      handleeRotateFn();
    };
    // 旋转处理
    const handleeRotateFn = () => {
      const img = document.getElementsByClassName('big-preview')[0];
      img.style.transform = `translate(-50%, -50%) rotate(${data.rotate}deg)`;
    };
    // 切换大图展示
    const switchFn = (type) => {
      const listNum = data.showList.length - 1; //战片总数
      let index = data.bigImgIndex;
      data.zoom = 90;
      handleeElargeFn();
      data.rotate = 0;
      handleeRotateFn();

      // 上一张
      if (type === 'prev') {
        data.bigImgIndex = index === 0 ? listNum : index - 1;
      } else {
        // 下一张
        data.bigImgIndex = index === listNum ? 0 : index + 1;
      }
      // 切换照片
      data.bigImgUrl = data.showList[data.bigImgIndex].photoImg;
    };
    return {
      rotateFn,
      enlargeFn,
      downloadImg,
      switchFn,
      offImg,
      switchNav,
      lookBigImg,
      ...toRefs(data)
    };
  }
};
</script>
<style scoped lang="less">
.my-album-page {
  width: 100%;
  height: 100%;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
  .my-album-box {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    .my-album-title {
      position: absolute;
      top: 0;
      left: 0;
      height: 50px;
      width: 100%;
      z-index: 1;
      border-radius: 4px;
      // background: #fff;
      // border-bottom: 1px solid #eee;
      box-sizing: border-box;
      padding: 0 20px;
      .title-nav-album {
        float: left;
        line-height: 50px;
        margin-right: 20px;
        cursor: pointer;
        color: #666;
        font-weight: 700;
        transition: 0.3s;
        &.active {
          color: #fff;
        }
      }
      .title-nav-photo {
        float: left;
        line-height: 50px;
        cursor: pointer;
        color: #666;
        font-weight: 700;
        transition: 0.3s;
        &.active {
          color: #fff;
        }
      }
    }
    .my-album-content-layout {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      padding-top: 60px;
      box-sizing: border-box;
      .my-album-content {
        height: 100%;
        // background: #fff;
        border-radius: 4px;
        overflow: auto;
        font-family: 'serif';
        padding: 2px 4px;
        &::-webkit-scrollbar {
          width: 7px;
        }
        /* 滚动槽 */
        &::-webkit-scrollbar-track {
          border-radius: 10px;
          background: rgb(224, 224, 224);
        }
        /* 滚动条滑块 */
        &::-webkit-scrollbar-thumb {
          border-radius: 10px;
          background: rgb(189, 189, 189);
        }

        // 自适应瀑布流开始
        .album-list {
          column-width: 300px;
          column-gap: 5px;
          width: 100%;
          max-width: 1920px;
          .album-list-item {
            position: relative;
            display: inline-block;
            // box-shadow: 0 1px 2px rgba(34, 25, 25, 0.4);
            // column-break-inside: avoid;
            border-radius: 5px;
            overflow: hidden;
            .album-list-item-img {
              transition: 2s;
              &:hover {
                cursor: pointer;
                transform: scale(2);
              }
              img {
                display: block;
                width: 100%;
                border-radius: 5px;
              }
            }

            // 自适应瀑布流结束
            .album-list-item-info {
              position: absolute;
              bottom: 0;
              left: 0;
              width: 100%;
              height: 60px;
              transition: 0.3s;
              background: rgba(0, 0, 0, 0.5);
              border-radius: 0 0 5px 5px;
              p {
                color: #fff;
                line-height: 40px;
                font-size: 18px;
                padding: 0 10px;
                font-style: oblique;
              }
              .item-left {
                float: left;
                .item-date {
                  line-height: 14px;
                  font-size: 12px;
                }
              }

              .item-num {
                float: right;
                font-size: 36px;
                line-height: 60px;
                padding: 0 20px;
              }
            }
            &:hover {
              .album-list-item-info {
                background: rgba(0, 0, 0, 0.8);
              }
            }
          }
        }
      }
    }
    .big-img-zzc {
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, 0.97);
      z-index: 33;
      .off-img-btn {
        position: absolute;
        top: 20px;
        right: 20px;
        color: #999;
        font-size: 30px;
        z-index: 9999;
        &:hover {
          color: #fff;
          cursor: pointer;
        }
      }
      .num-show {
        width: 200px;
        margin: 0 auto;
        color: #fff;
        font-size: 26px;
        line-height: 40px;
        text-align: center;
      }
      .tool-box {
        position: absolute;
        width: 336px;
        height: 40px;
        bottom: 0px;
        left: 50%;
        transform: translate(-50%);
        z-index: 9999;
        .download-btn,
        .enlarge-btn,
        .narrow-btn,
        .rotate-btn,
        .share-btn,
        .xinxi-btn {
          float: left;
          margin: 0 10px;
          width: 36px;
          height: 36px;
          text-align: center;
          line-height: 36px;
          color: #fff;
          font-size: 30px;
          transition: 0.3s;
          &:hover {
            transform: scale(1.2);
            cursor: pointer;
          }
        }
      }

      img {
        width: 90%;
        height: 90%;
        object-fit: contain;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      .switch-btn {
        position: absolute;
        top: 0;
        font-size: 30px;
        transition: 0.1s;
        width: 200px;
        line-height: 900px;
        height: 100%;
        &:hover {
          color: #fff;
          cursor: pointer;
        }
        &.icon-qiehuanqizuo {
          left: 20px;
        }
        &.icon-qiehuanqiyou {
          text-align: right;
          right: 20px;
        }
      }
    }
  }
}
</style>
